.CommentsList {
    background-color: #fff;
    overflow-y: auto;
    height: 70%;
}

.CommentsList::-webkit-scrollbar {
    width: 1em;
}

.CommentsList::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
}

.CommentsList::-webkit-scrollbar-thumb {
    background-color: darkgrey;
    outline: 1px solid slategrey;
}
.Comments {
    padding: 0 25px;
}

.Comments__text {
    font-size: 14px;
    line-height: 1.3;
    text-align: justify;
    padding: 5px;
}

.Comments__icon {
    padding-top:10px;
    max-width: 64px;
}

.Comments:last-child .divider {
    display:none;
}
