div.Header {
    background: #0E1D3C;
    width: 100%;
    color: #fff;
    text-align: left;
    height: 120px
}

.Header {
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -webkit-align-items: center;
    -ms-flex-align: center;
    align-items: center;
    padding: 0.875rem;
    overflow: hidden;
    text-overflow: ellipsis;
}

.Header-img_icon {
    cursor:pointer;
    margin: 5px 20px;
    height: auto;
    width: 50px;
}

.Header-img_icon:hover {
    -webkit-transition: all 100ms ease-in;
    -webkit-transform: scale(1.1);
    -ms-transition: all 100ms ease-in;
    -ms-transform: scale(1.1);
    -moz-transition: all 100ms ease-in;
    -moz-transform: scale(1.1);
    transition: all 100ms ease-in;
    transform: scale(1.1);
}

.Header-top__title {
    width: 100%;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    padding-right: 1rem;
    overflow: hidden;
}

.Header-top__title h2 {
    font-size: 2.6rem;
    margin: 0 0 0.125rem;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

h2.bigger  {
    font-size: 3.6rem;
    margin-left: 20px;
}

span.bigger {
    font-size: 1.8rem;
    margin-left: 20px;
}